body {
  background-color: #f3fad4;
  font-family: tahoma;
}

h1 {
  color: #91a832;
  text-transform: uppercase;
  text-shadow: 4px 4px 5px #999;
  margin-bottom: 40px;
}

h2 {
  letter-spacing: 2px;
}



.add {
  color: green;
  background: lightgreen;
  font-size: 14px;
  border: 1px solid #000;
  padding: 5px;
  margin-bottom: 10px;
  width: 40%;
}

.delete {
  color: red;
  background: pink;
  font-size: 14px;
  border: 1px solid #000;
  padding: 5px;
  margin-bottom: 10px;
  width: 40%;
}
